.buttonMenuArea {
    width: 100vw;
    height: auto;
    box-sizing: border-box;
    padding: 0 16px;

    .gray {
        font-size: 18px;
        color: #848c94;
        margin-top: 40px;
    }

    .whiteButtons {
        width: 100%;
        height: auto;

        ul {
            width: 100%;
            display: flex;
            align-items: center;
            // justify-content: center;
            flex-wrap: wrap;
            padding: 0;
        }

        li {
            width: 78px;
            height: 56px;
            list-style: none;
            box-sizing: border-box;
            border: 1px solid #dee2e6;
            text-align: center;
            padding: 5px 0;
            margin-right: 8px;
            margin-bottom: 8px;

            &:nth-child(4n) {
                margin-right: 0;
            }

            img {
                width: 24px;
                height: 24px;
            }

            p {
                font-size: 11px;
                margin-top: 2px;
                color: #343a40;
            }
        }
    }

    .grayButtons {
        width: 100%;
        height: auto;

        ul {
            width: 100%;
            display: flex;
            align-items: center;
            // justify-content: center;
            flex-wrap: wrap;
            padding: 0;
        }

        li {
            width: 78px;
            height: 56px;
            list-style: none;
            box-sizing: border-box;
            background-color: #f5f6f7;
            text-align: center;
            padding: 5px 0;
            margin-right: 8px;
            margin-bottom: 8px;

            &:nth-child(4n) {
                margin-right: 0;
            }

            img {
                width: 24px;
                height: 24px;
            }

            p {
                font-size: 11px;
                margin-top: 2px;
                color: #343a40;
            }
        }
    }
}

.advertise-container {
    width: 100vw;
    height: auto;
    box-sizing: border-box;
    padding-left: 12px;
    padding-bottom: 16px;

    .line {
        width: calc(100% - 32px);
        height: 1px;
        margin: 0 auto;
        margin-bottom: 16px;
        background-color: #e9ecef;
    }

    .adv-title {
        width: auto;
        // display: inline-block;
        font-size: 20px;
        color: #343a40;
        margin: 0;
        margin-bottom: 15px;
    }
    
    .adv-contents {
        width: 100%;
        margin-bottom: 20px;
        
        ul {
            width: 100%;
            overflow-x: scroll;
            list-style: none;
            margin: 0;
            padding: 0;
            display: flex;
            flex-wrap: nowrap;
            align-items: center;

            li {
                list-style: none;
                margin-right: 6px;

                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }
}